import Vue from 'vue'
import axios from 'axios'
import VueLazyLoad from 'vue-lazyload'
import  region from '../components/Region'
import  sort from '../components/Sort'
import {Toast} from "vant";
Vue.use(VueLazyLoad,{
  loading:require('../../../common/images/loading.jpg')
})
export default {
  name: "index",
  data() {
    return {
      search: '', //关键字
      showRegion:false, //是否显示地区
      showSort:false, // 是否显示分类
      amountOfPackage:1,//
      size:5,    //每页条数
      current:1, //当前页
      sortDefault:0, //分类
      region:'全国',//区域
      drugType1Name:'',
      enterpriseId:88888888,
      dismounting:0,//拆零
      sortprice:0,//价格排序
      ProductList:[], //产品列表
      loadFinsh:true //列表数据加载完毕
    }
  },
  components:{
    region,
    sort
  },
  methods: {
    //获取产品列表
    getList(){
      Toast.loading({
        message: '加载中...',
        forbidClick: this.loadFinsh
      });
      let str =`search=${this.search}&size=${this.size}&current=${this.current}&sortDefault=${this.sortDefault}&region=${this.region}&drugType1Name=${this.drugType1Name}`
      axios.get('http://api2.mall.xinquanjk.com/goods/search?'+str,{
      }).then(res => {
        if(res.status == 200){
          this.ProductList = res.data.data.page.records;
          this.loadFinsh = false;
          console.log(this.ProductList);
        }
      }).catch(e => {
        console.log(e);
      })
    },
    //关闭地区弹出框
    closeReginOverlay(){
      this.showRegion = false;
    },
    //关闭分类弹出框
    closeSortOverlay(){
      this.showSort = false;
    },
    //拆零
    dismounting(){
      this.dismounting =0;
      this.getList();
    }
  },
  created(){
    //获取列表数据
    this.getList();
  }
}



