import {Toast} from "vant";
export default {
  name: "index",
  data() {
    return {
      RegionList:['全国','河南省','湖北省','山西省','黑龙江省','福建省','江西省','山东省','广东省','海南省','云南省','甘肃省'] //地区列表
    }
  },
  methods: {
    //关闭地区弹出框
    closeRegion(){
      this.$parent.closeReginOverlay();
    },
    //选择地区
    chooseRegion(name){
      if(name == '全国'){
        this.$parent.closeReginOverlay();
      }else{
        Toast('不能跨省购买药品！');
      }
    }
  },
  created(){

  }
}



